@import url(https://fonts.googleapis.com/css?family=Merriweather:100,200,300,300i,400,400i,700);
@import url(https://fonts.googleapis.com/css?family=Roboto+Slab:400,700);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400);
body {
    font-family: "Open Sans", serif;
}

.wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    /* background: linear-gradient(to right, #e67e22, #e74c3c); */
    
    /* background-image: url("img/day.jpg");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat; */
}

.day {
    background-image: url(/react-weather/static/media/day.3f962f24.jpg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.night {
    background-image: url(/react-weather/static/media/night.0405dbe4.jpg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.main {
    display: flex;
    flex-direction: column;
    box-shadow: 0px 13px 40px -13px rgba(0, 0, 0, 0.75);
    width: 90%;
    max-width: 1100px;
    margin: 0 auto;
}

@media (min-width: 768px) {
    .main {
        flex-direction: row;
    }
}

#title-container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 200px;
    /* background: url("img/weather.jpg") center bottom no-repeat;
    background-size: cover; */
    text-align: center;
    color: #fff;
    text-shadow: 0 0 6px rgba(0, 0, 0, 0.8);
}

.standard-bg {
    background: url(/react-weather/static/media/weather.85d9f22e.jpg) center bottom no-repeat;
    background-size: cover;
}

.clear-d{
    background: url(/react-weather/static/media/clear-d.69833cf3.jpg) center no-repeat;
    background-size: cover;
}

.clear-n {
    background: url(/react-weather/static/media/clear-n.4dabb99a.jpg) center bottom no-repeat;
    background-size: cover;
}

.clouds-d {
    background-color: red;
    background: url(/react-weather/static/media/clouds-d.7307d5e4.jpg) center bottom no-repeat;
    background-size: cover;
}

.clouds-n {
    background: url(/react-weather/static/media/clouds-n.c4ba5748.jpg) center no-repeat;
    background-size: cover;
}

.drizzle-d {
    background: url(/react-weather/static/media/drizzle-d.b8f90930.jpg) center no-repeat;
    background-size: cover;
}

.drizzle-n {
    background: url(/react-weather/static/media/drizzle-n.45a98654.jpg) center no-repeat;
    background-size: cover;
}

.mist-d {
    background: url(/react-weather/static/media/mist-d.e3cd9948.jpg) center no-repeat;
    background-size: cover;
}

.mist-n {
    background: url(/react-weather/static/media/mist-n.e82692f6.jpg) center no-repeat;
    background-size: cover;
}

.rain-d {
    background: url(/react-weather/static/media/rain-d.d4be7d41.jpg) center no-repeat;
    background-size: cover;
}

.rain-n {
    background: url(/react-weather/static/media/rain-n.ebf33035.jpg) centerno-repeat;
    background-size: cover;
}

.snow-d {
    background: url(/react-weather/static/media/snow-d.cdbc6659.jpg) center no-repeat;
    background-size: cover;
}

.snow-n {
    background: url(/react-weather/static/media/snow-n.d53a0845.jpg) center no-repeat;
    background-size: cover;
}

.thunderstorm-d {
    background: url(/react-weather/static/media/thunderstorm-d.a478de8f.jpg) center no-repeat;
    background-size: cover;
}

.thunderstorm-n {
    background: url(/react-weather/static/media/thunderstorm-n.14ba95ef.jpg) center no-repeat;
    background-size: cover;
}

@media (min-width: 768px) {
    #title-container {
        width: 35%;
        height: 550px;
    }
}

.title-container__title {
    font-size: 32px;
    letter-spacing: 2px;
    line-height: 1.3;
    font-family: 'Roboto Slab', serif;
}

.title-container__subtitle {
    width:85%;
    margin: 0 auto;

    font-style: italic;
    font-weight: 100;
    letter-spacing: 1px;
    line-height: 1.5;
    font-family: "Merriweather", serif;
}

.form-container {
    display: flex;
    flex-direction: column;

    height: 360px;   
    background-color: #2c3e50;
    padding: 0.4em 0.8em;
}

@media (min-width: 768px) {
    .form-container {
        width: 65%;
        height: 550px;
        padding: 1em 2em;
    }
}

.form-container form {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;

    width: 100%;
}

@media (min-width: 414px) {
    .form-container form {
        /* justify-content: space-between; */
    }
}

input[type="text"] {
    display: block;
    width: 45%;

    margin: 1em 0;

    background-color: transparent;
    border: 0;
    border-bottom: solid 1px #f16051;
    padding-bottom: 4px;
    color: #fff !important;
    font-weight: lighter;
    letter-spacing: 2px;
    font-size: 14px;
}

@media (min-width: 414px) {
    input[type="text"] {
        font-size: 20px;
    }
}

@media (min-width: 768px) {
    input[type="text"] {
        width: 30%;
    }
}

input[type="text"] {
    outline: none;
}

input:-webkit-autofill {
    -webkit-box-shadow: 0 0 0 30px #2c3e50 inset;
    -webkit-text-fill-color: #fff !important;
}

button {
    display: block;
    width: 100%;
    height: 50px;

    border: 0;
    /* padding: 5px 12px; */
    border-radius: 2px;
    font-weight: lighter;
    letter-spacing: 1px;
    font-size: 18px;
    cursor: pointer;
    background-color: #f16051;
    color: #fff;
    font-weight: 500;
}

@media (min-width: 768px) {
    button {
        width: 30%;
        /* padding: 2px 10px; */
        font-size: 20px;
    }
}

button:active {
    outline: none;
}

.weather__info {
    display: block;
    width: 100%;
    font-size: 14px;
    font-weight: 200;
    letter-spacing: 2px;

    padding-top: 0.5em;
}

@media (min-width: 768px) {
    .weather__info {
        font-size: 18px;
    }
}

.weather__key {
    color: #f16051;
    border-bottom: solid 2px rgba(255, 255, 255, 0.06);
    padding: 8px 0 8px 0;
    font-weight: 400;
}

@media (min-width: 768px) {
    .weather__key {
        padding: 20px 0 20px 0;
    }
}

.weather__key:last-child {
    border: 0;
}

.weather__value {
    color: #fff;
    font-weight: 200;
}

.weather__error {
    color: #f16051;
    font-size: 20px;
    letter-spacing: 1px;
    font-weight: 200;
}
